.concept {
  &__content-wrapper {
    margin-top: 120px;
  }

  &__content {
    & h3 {
      margin: 0;

      font-family: "Info", "Arial", sans-serif;
      font-weight: 400;
      font-size: 18px;
      text-transform: uppercase;
    }
  }

  &__content p {
    margin: 30px 0 0;
    line-height: 21px;
  }
}

@media (min-width: 768px) {
  .concept {
    &__content-wrapper {
      margin-top: 70px;
    }

    &__content {
      & h3 {
        font-size: 16px;
      }
    }

    &__content p {
      margin: 14px 0 0;

      font-size: 14px;
      line-height: 26px;
    }
  }
}

@media (min-width: 1024px) {
  .concept {
    &__content-wrapper {
      margin-top: 97px;
    }

    &__content {
      // width: 855px;

      & h3 {
        font-size: 20px;
      }
    }

    &__content p {
      margin: 23px 0 0;

      font-size: 16px;
    }
  }
}

@media (min-width: 1280px) {
  .concept {
    &__content-wrapper {
      margin-top: 152px;
    }

    &__content {
      // width: 970px;

      & h3 {
        font-size: 25px;
      }
    }

    &__content p {
      margin: 33px 0 0;

      line-height: 21px;
    }
  }
}

@media (min-width: 1920px) {
  .concept {
    &__content-wrapper {
      margin-top: 326px;
    }

    &__content {
      width: auto;

      & h3 {
        font-size: 38px;
      }
    }

    &__content p {
      margin: 57px 0 0;

      font-size: 20px;
      line-height: 26px;
    }
  }
}
